@import lib/mixins
.g-badge
	+r(1000)
		right: 20px 
	+r(750)
		display: none
.promo
	+r(750)
		background: #229fff !important
		margin-bottom: 30px 
		h2
			font-size: 42px
			line-height: 1.1 
		.wrapper
			color: #fff 
			text-align: center
		.g-button_blue
			background: #fdda24 
			color: #434e55 
		.icons
			position: static
			margin-top: 30px 
			color: #fff 
			span
				color: #fff 
		[style="margin-top: 140px;font-weight: 700;"]
			margin-top: 20px !important 
	.additional
		+r(750)
			position: static
	+r(500)
		.icons
			p
				display: none
.bar
	+r(1070)
		margin: 20px 0 10px 0 
		padding: 0 20px 
	+r(500)
		h2
			font-size: 24px 
			line-height: 1.2 
	.wrapper
		+r(1070)
			width: auto !important
#devices_list
	+r(1070)
		width: auto
		padding: 0 20px 
	+r(800)
		.illustration
			display: none
	.device
		+r(800)
			&:before
				position: static
				display: inline-block
				vertical-align: middle
				margin-right: 10px 
			p
				width: auto
				margin-bottom: 20px 
			h2
				display: inline-block
				margin: 0
				padding: 0
				vertical-align: middle  
				width: auto
		+r(500)
			padding: 20px 0
			&:before
				display: block
			h2
				display: block
				margin-top: 10px 
				font-size: 28px 
				line-height: 1.2 
			p a.app-store-btn
				margin-right: 0 
